import React, { useState } from "react";
import { Button, Form, Spinner } from "react-bootstrap";
import logo from "../../../assets/images/main-logo.svg";
import { SocialLogin } from "../Social";
import { useDispatch, useSelector } from "react-redux";
import { requestLogin } from "../../../../redux/actions";
import { getValidationErrors } from "../../../../utills/helpers";
import { LoginValidation } from "../../../validations";
const LoginFormComponent = ({ onSignUp,onFogotPassowrd }) => {
  const [formValues, setFormValues] = useState({
    email: "",
    password: "",
  });
  const [formErrors, setFormErrors] = useState({
    email: "",
    password: "",
  });
  const dispatch = useDispatch();
  const { isLoadingAuth: isLoading } = useSelector(
    (state) => state.authReducer
  );
  /**
   *
   * @param {*} e
   */
  const onInputChange = (e) => {
    //
    const { name, value } = e.target;
    const newFormValues = Object.assign({}, formValues);
    newFormValues[name] = value;
    setFormValues(newFormValues);
    // for errors
    const newFormErrors = Object.assign({}, formErrors);
    newFormErrors[name] = "";
    setFormErrors(newFormErrors);
  };
  /**
   *
   * @param {*} e
   */
  const login = (e) => {
    e.preventDefault();
    try {
      LoginValidation.validateSync(formValues, {
        abortEarly: false,
      });
      dispatch(requestLogin(formValues));
    } catch (error) {
      setFormErrors(getValidationErrors(error));
    }
  };
  return (
    <>
      <div className="auth-form-wrap">
        <div className="auth-logo text-center mb-2 mt-2">
          <img src={logo} width={160} alt={"Main Logo"} />
        </div>
        <h5 className="mb-3 text-center text-capitalize">
          Enter Login Details
        </h5>
        <Form onSubmit={login} noValidate>
          <Form.Group controlId="formBasicEmail">
            <Form.Control
              type="email"
              placeholder=""
              onChange={onInputChange}
              name="email"
              value={formValues.email}
            />
            {/* <Form.Label>Email</Form.Label> */}
            <label className={`form-control-placeholder ${formValues.email ? "float-label" : ""}`}>
              Email
            </label>
            {formErrors.email ? (
              <p className="text-danger">{formErrors.email}</p>
            ) : null}
          </Form.Group>
          <Form.Group controlId="formBasicPassword">
            <Form.Control
              type="password"
              placeholder=""
              name="password"
              onChange={onInputChange}
              value={formValues.password}
            />
            {/* <Form.Label>Password</Form.Label> */}
            <label className={`form-control-placeholder ${formValues.password ? "float-label" : ""}`}>
              Password
            </label>
            {formErrors.password ? (
              <p className="text-danger">{formErrors.password}</p>
            ) : null}

          </Form.Group>
          <div className="auth-links d-block d-md-flex  text-center align-items-center justify-content-between mb-3">
            <p className="mb-3 mb-md-0">
              Not a member ?
              <a
                href="/"
                onClick={(e) => {
                  e.preventDefault();
                  onSignUp();
                }}
              >
                {" "}
                Register here.
              </a>
            </p>
            <a
              href="/"
              onClick={(e) => {
                e.preventDefault();
                onFogotPassowrd()
              }}
            >
              Forgot Password?
            </a>
          </div>
          <Button
            variant="primary"
            type="submit"
            className="w-100"
            disabled={isLoading}
          >
            {isLoading ? (
              <>
                <Spinner />
                &nbsp; Please wait...
              </>
            ) : (
                <>Login</>
              )}{" "}
          </Button>
          <SocialLogin />
        </Form>
      </div>
    </>
  );
};

export default LoginFormComponent;
